
import React from 'react';
import { View } from '../types';
import Button from './Button';

interface StoreScreenProps {
  navigate: (view: View) => void;
  onPurchaseRemoveAds: () => void;
  onPurchasePremium: () => void;
  showAds: boolean;
  hasPremium: boolean;
}

const StoreItem: React.FC<{
  icon: string;
  title: string;
  description: string;
  price: string;
  onClick: () => void;
  disabled?: boolean;
  disabledText?: string;
}> = ({ icon, title, description, price, onClick, disabled, disabledText }) => (
  <div className={`bg-slate-800/50 rounded-lg p-6 flex flex-col items-center text-center backdrop-blur-sm border border-slate-700 transition-all ${disabled ? 'opacity-60' : ''}`}>
    <i className={`text-5xl mb-4 text-cyan-400 ${icon}`}></i>
    <h3 className="text-xl font-bold mb-2">{title}</h3>
    <p className="text-slate-400 mb-4 flex-grow">{description}</p>
    {disabled ? (
        <p className="text-lg font-bold text-green-400">{disabledText || '¡Comprado!'}</p>
    ) : (
      <Button onClick={onClick} fullWidth>
        {price}
      </Button>
    )}
  </div>
);


const StoreScreen: React.FC<StoreScreenProps> = ({ navigate, onPurchaseRemoveAds, onPurchasePremium, showAds, hasPremium }) => {
    
  // Handle purchases: prefer native in-app billing (mobile) and fall back to Stripe on web
  const handlePurchase = async (purchaseFunction: () => void, paymentType: 'removeAds' | 'premium') => {
    console.log(`Initiating ${paymentType} purchase flow...`);

    const isNative = (window as any).Capacitor?.isNativePlatform?.() ?? false;

    try {
      if (isNative) {
        // Native path: use cordova-plugin-purchase wrapper we added (services/purchaseService.ts)
        const purchaseModule = await import('../services/purchaseService');
        // Product IDs must be configured in Play Console; replace these placeholders as needed
        const productId = paymentType === 'removeAds' ? 'remove_ads' : 'premium_phrases';
        const res = await purchaseModule.buyProduct(productId);
        if (res) {
          purchaseFunction();
        }
      } else {
        // Web path: use existing Stripe flow
        const { handleRemoveAdsPayment, handlePremiumPayment } = await import('../services/stripeService');
        let success = false;
        if (paymentType === 'removeAds') {
          success = await handleRemoveAdsPayment();
        } else if (paymentType === 'premium') {
          success = await handlePremiumPayment();
        }
        if (success) purchaseFunction();
      }
    } catch (error) {
      console.error('Payment error:', error);
      alert('No se pudo procesar el pago. Inténtalo más tarde.');
    }
  };
    
  return (
    <div className="flex flex-col w-full h-full p-4 animate-fadeIn">
      <div className="flex justify-between items-center mb-8">
        <h2 className="text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-cyan-400 to-pink-500">
          Tienda
        </h2>
        <button onClick={() => navigate(View.Home)} className="text-slate-400 hover:text-white transition-colors">
          <i className="fas fa-times fa-2x"></i>
        </button>
      </div>

      <div className="space-y-6">
        <StoreItem
          icon="fas fa-shield-halved"
          title="Eliminar Anuncios"
          description="Disfruta de una experiencia de juego ininterrumpida sin anuncios."
          price="Comprar por 2.99€"
          onClick={() => handlePurchase(onPurchaseRemoveAds, 'removeAds')}
          disabled={!showAds}
        />
        <StoreItem
          icon="fas fa-star"
          title="Paquete Premium"
          description="Desbloquea cientos de frases extra, más atrevidas y divertidas."
          price="Comprar por 4.99€"
          onClick={() => handlePurchase(onPurchasePremium, 'premium')}
          disabled={hasPremium}
        />
      </div>
      <p className="text-center text-slate-500 text-xs mt-8">
        Las compras son procesadas de forma segura a través de Stripe.
      </p>
    </div>
  );
};

export default StoreScreen;
