
import React, { useState } from 'react';
import { addSuggestion } from '../services/firebaseService';
import Button from './Button';
import Modal from './Modal';

interface SuggestPhraseModalProps {
  onClose: () => void;
}

const SuggestPhraseModal: React.FC<SuggestPhraseModalProps> = ({ onClose }) => {
  const [phrase, setPhrase] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (phrase.trim().length < 10) {
      setError('La frase debe tener al menos 10 caracteres.');
      return;
    }
    setError('');
    setIsSubmitting(true);
    try {
      await addSuggestion(phrase);
      setSuccess(true);
      setTimeout(() => {
        onClose();
      }, 2000);
    } catch (err) {
      setError('No se pudo enviar la sugerencia. Inténtalo de nuevo.');
      setIsSubmitting(false);
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className="p-6">
        {success ? (
          <div className="text-center">
            <i className="fas fa-check-circle text-5xl text-green-400 mb-4"></i>
            <h2 className="text-2xl font-bold mb-2">¡Gracias!</h2>
            <p className="text-slate-300">Tu sugerencia ha sido enviada para revisión.</p>
          </div>
        ) : (
          <>
            <h2 className="text-2xl font-bold mb-4">Sugerir una Frase</h2>
            <p className="text-slate-400 mb-6">¿Tienes una idea genial? ¡Compártela con nosotros! Si es aprobada, aparecerá en el juego.</p>
            <form onSubmit={handleSubmit}>
              <div className="relative">
                <span className="absolute left-4 top-3 text-slate-400">Yo nunca...</span>
                <input
                  type="text"
                  value={phrase}
                  onChange={(e) => setPhrase(e.target.value)}
                  placeholder="he hecho..."
                  className="w-full bg-slate-700 border border-slate-600 rounded-md py-3 pr-4 pl-36 focus:ring-2 focus:ring-cyan-400 focus:outline-none transition"
                  disabled={isSubmitting}
                />
              </div>
              {error && <p className="text-red-400 text-sm mt-2">{error}</p>}
              <div className="mt-6 flex justify-end space-x-4">
                <Button type="button" variant="secondary" onClick={onClose} disabled={isSubmitting}>
                  Cancelar
                </Button>
                <Button type="submit" disabled={isSubmitting || phrase.trim() === ''}>
                  {isSubmitting ? (
                    <>
                      <i className="fas fa-spinner fa-spin mr-2"></i>
                      Enviando...
                    </>
                  ) : 'Enviar Sugerencia'}
                </Button>
              </div>
            </form>
          </>
        )}
      </div>
    </Modal>
  );
};

export default SuggestPhraseModal;
