
import React, { useState, useEffect, useCallback } from 'react';
import { Phrase, View } from '../types';
import { fetchPhrases } from '../services/firebaseService';
import Button from './Button';

interface GameScreenProps {
  navigate: (view: View) => void;
  hasPremium: boolean;
  includeAdult?: boolean;
}

const GameScreen: React.FC<GameScreenProps> = ({ navigate, hasPremium, includeAdult }) => {
  const [phrases, setPhrases] = useState<Phrase[]>([]);
  const [currentPhrase, setCurrentPhrase] = useState<Phrase | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const loadPhrases = async () => {
      try {
        setIsLoading(true);
        setError(null);
        // Aquí se utilizará automáticamente el sistema de caché implementado
        const fetchedPhrases = await fetchPhrases(hasPremium, !!includeAdult);
        
        if (fetchedPhrases.length === 0) {
            setError('No se encontraron frases. Inténtalo más tarde.');
        } else {
            setPhrases(fetchedPhrases);
            setCurrentPhrase(fetchedPhrases[Math.floor(Math.random() * fetchedPhrases.length)]);
        }
      } catch (e) {
        setError('No se pudieron cargar las frases. Revisa tu conexión.');
        console.error(e);
      } finally {
        setIsLoading(false);
      }
    };
    loadPhrases();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [hasPremium]);

  const showNextPhrase = useCallback(() => {
    if (phrases.length > 0) {
      const remainingPhrases = phrases.filter(p => p.id !== currentPhrase?.id);
      if (remainingPhrases.length > 0) {
        const next = remainingPhrases[Math.floor(Math.random() * remainingPhrases.length)];
        setCurrentPhrase(next);
      } else {
        // If all phrases have been seen, just pick a random one again
        const next = phrases[Math.floor(Math.random() * phrases.length)];
        setCurrentPhrase(next);
      }
    }
  }, [phrases, currentPhrase]);

  const renderContent = () => {
    if (isLoading) {
      return (
        <div className="flex flex-col items-center justify-center h-full">
            <i className="fas fa-spinner fa-spin text-5xl text-cyan-400"></i>
            <p className="mt-4 text-slate-300">Cargando frases...</p>
        </div>
      )
    }

    if (error) {
        return <p className="text-center text-red-400">{error}</p>
    }

    return (
        currentPhrase && (
            <div className="text-center text-2xl md:text-3xl font-semibold leading-relaxed animate-fadeIn" key={currentPhrase.id}>
                <span className="text-slate-400">Yo nunca...</span>
                <br/>
                <span className="text-white drop-shadow-lg">{currentPhrase.text}</span>
            </div>
        )
    )
  }

  return (
    <div className="flex flex-col items-center justify-between h-full w-full p-4">
      <div className="w-full flex justify-start">
        <button onClick={() => navigate(View.Home)} className="text-slate-400 hover:text-white transition-colors">
          <i className="fas fa-arrow-left fa-lg"></i>
        </button>
      </div>

      <div className="flex-grow flex items-center justify-center w-full max-w-md my-8">
        {renderContent()}
      </div>

      <div className="w-full max-w-xs">
        <Button onClick={showNextPhrase} disabled={isLoading || phrases.length <= 1} fullWidth size="large">
            <i className="fas fa-forward mr-2"></i>
            Siguiente
        </Button>
      </div>
    </div>
  );
};

export default GameScreen;
