import { initializeApp } from 'firebase/app';
import { getFirestore } from 'firebase/firestore';

// Función para leer variables de entorno, ya sea en Vite o en Node
const getEnv = (key: string): string | undefined => {
  // Vite: import.meta.env (cast a any para evitar errores de TS en entornos donde ImportMeta no tiene 'env')
  const meta: any = typeof import.meta !== 'undefined' ? import.meta : undefined;
  if (meta && meta.env && meta.env[key]) {
    return meta.env[key];
  }
  // Node: process.env
  if (typeof process !== 'undefined' && process.env[key]) {
    return process.env[key]!;
  }
  return undefined;
};

// Intentamos leer las variables VITE_* necesarias. Si faltan, no inicializamos Firebase
const missingKeys: string[] = [];
const envVars = {
  apiKey: getEnv('VITE_FIREBASE_API_KEY'),
  authDomain: getEnv('VITE_FIREBASE_AUTH_DOMAIN'),
  projectId: getEnv('VITE_FIREBASE_PROJECT_ID'),
  storageBucket: getEnv('VITE_FIREBASE_STORAGE_BUCKET'),
  messagingSenderId: getEnv('VITE_FIREBASE_MESSAGING_SENDER_ID'),
  appId: getEnv('VITE_FIREBASE_APP_ID')
};

Object.entries(envVars).forEach(([k, v]) => {
  if (!v) missingKeys.push(k);
});

export const FIREBASE_ENABLED = missingKeys.length === 0;

let db: any = null;

if (!FIREBASE_ENABLED) {
  // Mensaje claro para el desarrollador / CI
  console.warn('[firebaseConfig] Firebase no configurado. Las siguientes claves VITE_* faltan o no estuvieron disponibles en tiempo de build:', missingKeys);
  console.warn('[firebaseConfig] Para desarrollo local crea un archivo `.env.local` en la raíz con las variables VITE_FIREBASE_*. En CI asegúrate de inyectarlas antes de la compilación. Ver `./.env.example` como referencia.');
} else {
  const firebaseConfig = {
    apiKey: envVars.apiKey!,
    authDomain: envVars.authDomain!,
    projectId: envVars.projectId!,
    storageBucket: envVars.storageBucket!,
    messagingSenderId: envVars.messagingSenderId!,
    appId: envVars.appId!
  };

  // Initialize Firebase
  const app = initializeApp(firebaseConfig);
  db = getFirestore(app);
}

export { db };
