import React, { useState, useCallback, useEffect } from 'react';
import { initMobApp } from './services/mobAppService';
import HomeScreen from './components/HomeScreen';
import GameScreen from './components/GameScreen';
import StoreScreen from './components/StoreScreen';
import SuggestPhraseModal from './components/SuggestPhraseModal';
import AdBanner from './components/AdBanner';
import { View } from './types';
import { updatePhraseCache } from './services/firebaseService';

// Import Capacitor AdMob solo en código TS/TSX
import { AdMob, BannerAdOptions, BannerAdSize } from '@capacitor-community/admob';

const App: React.FC = () => {
  const [currentView, setCurrentView] = useState<View>(View.Home);
  const [showSuggestModal, setShowSuggestModal] = useState(false);
  const [showAds, setShowAds] = useState(true);
  const [hasPremium, setHasPremium] = useState(false);
  const [includeAdult, setIncludeAdult] = useState(false);

  useEffect(() => {
    // Inicializar MobApp SDK
    initMobApp();

    // Inicializar caché de frases al arrancar la app
    const initCache = async () => {
      try {
        console.log('Inicializando caché de frases al arrancar la app');
        await updatePhraseCache(true);
      } catch (error) {
        console.error('Error inicializando caché de frases:', error);
      }
    };
    
    initCache();

    // Mostrar Banner AdMob si corresponde
    if (showAds) {
      const bannerOptions: BannerAdOptions = {
        adId: process.env.VITE_MOBAPP_BANNER_ID || 'ca-app-pub-3940256099942544/6300978111', // AdMob test ID si no está configurado
        adSize: BannerAdSize.BANNER,
        position: 'BOTTOM_CENTER',
        margin: 0,
      };
      AdMob.showBanner(bannerOptions)
        .then(() => console.log('Banner AdMob mostrado'))
        .catch(err => console.error('Error mostrando banner (mobile):', err));
    }

    return () => {
      AdMob.hideBanner().catch(() => {});
    };
  }, [showAds]);
  
  // Efecto para actualizar el caché cuando cambia el estado premium
  useEffect(() => {
    const updateCache = async () => {
      try {
        console.log(`Estado premium cambiado: ${hasPremium}, actualizando caché...`);
        await updatePhraseCache(true);
      } catch (error) {
        console.error('Error actualizando caché después de cambio premium:', error);
      }
    };
    
    updateCache();
  }, [hasPremium]);

  const navigate = useCallback((view: View) => {
    setCurrentView(view);
  }, []);

  const handlePurchaseRemoveAds = () => {
    setShowAds(false);
    AdMob.hideBanner().catch(() => {});
    alert('¡Gracias! Los anuncios han sido eliminados.');
    setCurrentView(View.Home);
  };

  const handlePurchasePremium = () => {
    setHasPremium(true);
    alert('¡Gracias! Has comprado el paquete de frases premium.');
    setCurrentView(View.Home);
  };

  const renderView = () => {
    switch (currentView) {
      case View.Game:
        return <GameScreen navigate={navigate} hasPremium={hasPremium} includeAdult={includeAdult} />;
      case View.Store:
        return (
          <StoreScreen
            navigate={navigate}
            onPurchaseRemoveAds={handlePurchaseRemoveAds}
            onPurchasePremium={handlePurchasePremium}
            showAds={showAds}
            hasPremium={hasPremium}
          />
        );
      case View.Home:
      default:
        return <HomeScreen navigate={navigate} onSuggestClick={() => setShowSuggestModal(true)} onStartGame={(v) => setIncludeAdult(v)} />;
    }
  };

  return (
    <div className="relative min-h-screen w-full flex flex-col items-center justify-center p-4 bg-gradient-to-br from-slate-900 via-indigo-950 to-slate-900 overflow-hidden">
      <div className="absolute top-0 left-0 w-full h-full bg-[url('https://www.transparenttextures.com/patterns/stardust.png')] opacity-20"></div>

      <div className="z-10 w-full max-w-md mx-auto flex-grow flex flex-col justify-center">
        {renderView()}
      </div>

      {showSuggestModal && <SuggestPhraseModal onClose={() => setShowSuggestModal(false)} />}
    </div>
  );
};

export default App;
