import React, { useState } from 'react';
import { View } from '../types';
import Button from './Button';

interface HomeScreenProps {
  navigate: (view: View) => void;
  onSuggestClick: () => void;
  onStartGame: (includeAdult: boolean) => void;
}

const HomeScreen: React.FC<HomeScreenProps> = ({ navigate, onSuggestClick, onStartGame }) => {
  const [includeAdult, setIncludeAdult] = useState(false);
  const [copied, setCopied] = useState(false);

  const shareApp = async () => {
    const appLink = 'https://play.google.com/store/apps/details?id=com.mrodr.yonunca';
    
    if (navigator.share) {
      try {
        await navigator.share({
          title: 'Yo Nunca',
          text: '¡Descarga Yo Nunca y juega con tus amigos!',
          url: appLink,
        });
      } catch (err) {
        console.error('Error compartiendo la app:', err);
      }
    } else {
      // Fallback para copiar al portapapeles
      try {
        await navigator.clipboard.writeText(appLink);
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
      } catch (err) {
        console.error('No se pudo copiar al portapapeles', err);
      }
    }
  };

  return (
    <div className="flex flex-col items-center justify-center h-full text-center p-4 animate-fadeIn">
      <h1 className="text-6xl md:text-7xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-cyan-400 to-pink-500 mb-4 drop-shadow-[0_0_10px_rgba(255,255,255,0.2)]">
        Yo Nunca
      </h1>
      <p className="text-slate-300 mb-12 text-lg">El juego para revelar secretos.</p>
      
      <div className="w-full max-w-xs space-y-4">
        <Button onClick={() => { onStartGame(includeAdult); navigate(View.Game); }} fullWidth size="large">
          <i className="fas fa-play mr-2"></i>
          Iniciar Partida
        </Button>
        <div className="flex items-center justify-center gap-2 mt-2">
          <input id="include-adult" type="checkbox" checked={includeAdult} onChange={(e) => setIncludeAdult(e.target.checked)} className="h-5 w-5" />
          <label htmlFor="include-adult" className="text-sm text-slate-300">Incluir +18</label>
        </div>
        <Button onClick={onSuggestClick} variant="secondary" fullWidth>
          <i className="fas fa-lightbulb mr-2"></i>
          Sugerir Frase
        </Button>
        <Button onClick={() => navigate(View.Store)} variant="secondary" fullWidth>
          <i className="fas fa-store mr-2"></i>
          Tienda
        </Button>
        <Button onClick={shareApp} variant="secondary" fullWidth>
          <i className="fas fa-share-alt mr-2"></i>
          {copied ? '¡Enlace copiado!' : 'Compartir App'}
        </Button>
      </div>
    </div>
  );
};

export default HomeScreen;
