// Servicio para anuncios, compatible con web y móvil

// Interface for ad configuration
interface AdConfig {
  appId: string;
  bannerId: string;
  interstitialId: string;
}

// Get ad configuration from environment variables
const getAdConfig = (): AdConfig => {
  return {
    appId: process.env.VITE_MOBAPP_APP_ID,
    bannerId: process.env.VITE_MOBAPP_BANNER_ID,
    interstitialId: process.env.VITE_MOBAPP_INTERSTITIAL_ID,
  };
};

// Initialize MobApp SDK
export const initMobApp = async (): Promise<void> => {
  if ((window as any).Capacitor?.isNativePlatform?.()) {
    const admob = await import('../mobile/admobNative');
    await admob.initMobApp();
  }
};

// Load and show banner ad
export const showBannerAd = async (): Promise<void> => {
  if ((window as any).Capacitor?.isNativePlatform?.()) {
    const admob = await import('../mobile/admobNative');
    await admob.showBannerAd();
  }
};

// Load and show interstitial ad
export const showInterstitialAd = (): Promise<boolean> => {
  return new Promise((resolve) => {
    const config = getAdConfig();
    
    // In a real implementation, this would show an interstitial ad
    // Example: MobApp.showInterstitial(config.interstitialId, resolve);
    console.log('Showing interstitial ad with ID:', config.interstitialId);
    
    // Simulate ad display
    setTimeout(() => {
      console.log('Interstitial ad closed');
      resolve(true);
    }, 1000);
  });
};

// Hide banner ad
export const hideBannerAd = async (): Promise<void> => {
  if ((window as any).Capacitor?.isNativePlatform?.()) {
    const admob = await import('../mobile/admobNative');
    await admob.hideBannerAd();
  }
};