import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import { Capacitor } from '@capacitor/core';
import { AdMob } from '@capacitor-community/admob';

// Inicialización de AdMob y Capacitor
const initializeApp = async () => {
  // Solo en dispositivo móvil
  if (Capacitor.isNativePlatform()) {
    try {
      await AdMob.initialize();
      console.log('AdMob inicializado correctamente');
    } catch (err) {
      console.error('Error inicializando AdMob:', err);
    }
  }
};

// Ejecutar inicialización antes de montar React
initializeApp()
  .then(() => {
    const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement);
    root.render(
      <React.StrictMode>
        <App />
      </React.StrictMode>
    );
  })
  .catch(err => console.error('Error al iniciar la app:', err));
