import { Phrase } from '../types';
import { collection, getDocs, addDoc, query, where, doc, getDoc } from 'firebase/firestore';
import { db, FIREBASE_ENABLED } from './firebaseConfig';

// Fallback mock data in case Firebase connection fails
const mockPhrases: Phrase[] = [
    { id: '1', text: 'Yo nunca he fingido que me gustaba un regalo.', isPremium: false },
    { id: '2', text: 'Yo nunca he espiado el móvil de mi pareja.', isPremium: false },
    { id: '3', text: 'Yo nunca he usado el cepillo de dientes de otra persona.', isPremium: false },
    { id: '4', text: 'Yo nunca he vuelto con un ex.', isPremium: false },
    { id: '5', text: 'Yo nunca me he reído en un momento inoportuno.', isPremium: false },
    // Reduced mock data for fallback only
]

// Sistema de caché para frases
interface PhraseCache {
    freePhrases: Phrase[];
    premiumPhrases: Phrase[];
    adultPhrases: Phrase[];
    allPhrases: Phrase[];
    lastUpdated: number;
}

// Inicializar caché vacío
const phraseCache: PhraseCache = {
    freePhrases: [],
    premiumPhrases: [],
    adultPhrases: [],
    allPhrases: [],
    lastUpdated: 0
};

// Tiempo de expiración de caché en milisegundos (1 hora)
const CACHE_EXPIRATION_TIME = 60 * 60 * 1000;

// Función para verificar si el caché está actualizado
const isCacheValid = (): boolean => {
    const now = Date.now();
    return phraseCache.lastUpdated > 0 && (now - phraseCache.lastUpdated) < CACHE_EXPIRATION_TIME;
};

// Función para actualizar el caché de frases
export const updatePhraseCache = async (forceUpdate: boolean = false): Promise<void> => {
    // Si el caché es válido y no se fuerza la actualización, no hacemos nada
    if (isCacheValid() && !forceUpdate) {
        console.log('Caché de frases válido, omitiendo actualización');
        return;
    }

    if (!FIREBASE_ENABLED) {
        console.warn('[updatePhraseCache] Firebase no está configurado, usando datos mock.');
        phraseCache.freePhrases = mockPhrases.filter(p => !p.isPremium);
        phraseCache.premiumPhrases = mockPhrases.filter(p => p.isPremium);
        phraseCache.adultPhrases = [];
        phraseCache.allPhrases = mockPhrases;
        phraseCache.lastUpdated = Date.now();
        return;
    }

    try {
        console.log('Actualizando caché de frases...');
        
        // Obtener todas las frases
        const baseCollection = collection(db, 'phrases');
        const querySnapshot = await getDocs(baseCollection);
        
        // Limpiar caché actual
        phraseCache.freePhrases = [];
        phraseCache.premiumPhrases = [];
        phraseCache.adultPhrases = [];
        phraseCache.allPhrases = [];
        
        // Procesar resultados y clasificar frases
        querySnapshot.forEach((doc) => {
            const phrase: Phrase = {
                id: doc.id,
                ...doc.data() as Omit<Phrase, 'id'>
            };
            
            // Añadir a la lista completa
            phraseCache.allPhrases.push(phrase);
            
            // Clasificar según tipo
            if (phrase.isPremium) {
                phraseCache.premiumPhrases.push(phrase);
            } else {
                phraseCache.freePhrases.push(phrase);
            }
            
            // Clasificar frases para adultos
            if (phrase.isAdult) {
                phraseCache.adultPhrases.push(phrase);
            }
        });
        
        // Actualizar timestamp
        phraseCache.lastUpdated = Date.now();
        console.log(`Caché actualizado: ${phraseCache.allPhrases.length} frases en total`);
        
    } catch (error) {
        console.error('Error actualizando caché de frases:', error);
        // En caso de error, usar datos mock como fallback
        phraseCache.freePhrases = mockPhrases.filter(p => !p.isPremium);
        phraseCache.premiumPhrases = mockPhrases.filter(p => p.isPremium);
        phraseCache.adultPhrases = [];
        phraseCache.allPhrases = mockPhrases;
        phraseCache.lastUpdated = Date.now();
    }
};

// Función para obtener frases desde el caché
export const fetchPhrases = async (includePremium: boolean, includeAdult: boolean = false): Promise<Phrase[]> => {
    console.log(`Fetching phrases... premium=${includePremium} adult=${includeAdult}`);
    
    // Si Firebase no está habilitado, devolvemos mock
    if (!FIREBASE_ENABLED) {
        console.warn('[fetchPhrases] Firebase no está configurado, devolviendo datos mock.');
        if (includePremium) return mockPhrases;
        return mockPhrases.filter(p => !p.isPremium);
    }

    try {
        // Si el caché no es válido o está vacío, actualizarlo
        if (!isCacheValid() || phraseCache.allPhrases.length === 0) {
            await updatePhraseCache(true);
        }
        
        // Filtrar frases según los parámetros
        let result: Phrase[] = [];
        
        if (includePremium && includeAdult) {
            // Todas las frases
            result = [...phraseCache.allPhrases];
        } else if (includePremium && !includeAdult) {
            // Todas excepto adultos
            result = phraseCache.allPhrases.filter(p => !p.isAdult);
        } else if (!includePremium && includeAdult) {
            // Solo gratuitas, incluyendo adultos
            result = phraseCache.freePhrases;
        } else {
            // Solo gratuitas, excluyendo adultos
            result = phraseCache.freePhrases.filter(p => !p.isAdult);
        }
        
        return result;
    } catch (error) {
        console.error('Error obteniendo frases desde caché:', error);
        // Return mock data as fallback
        if (includePremium) {
            return mockPhrases;
        }
        return mockPhrases.filter(p => !p.isPremium);
    }
};

export const addSuggestion = async (phraseText: string): Promise<void> => {
    console.log(`Adding suggestion: "${phraseText}"`);
    try {
        if (!FIREBASE_ENABLED) {
            console.warn('[addSuggestion] Firebase no está configurado. La sugerencia no se enviará en este entorno.');
            throw new Error('Firebase no configurado en este entorno.');
        }

        await addDoc(collection(db, "suggestions"), {
            text: phraseText,
            createdAt: new Date(),
            status: 'pending'
        });
        console.log('Suggestion submitted successfully.');
    } catch (error) {
        console.error('Error adding suggestion:', error);
        throw new Error('No se pudo enviar la sugerencia. Inténtalo más tarde.');
    }
};

// Función para obtener las claves de productos de Stripe desde Firestore
export const getStripeProductKeys = async (): Promise<{[key: string]: string}> => {
    try {
        if (!FIREBASE_ENABLED) {
            console.warn('[getStripeProductKeys] Firebase no está configurado, devolviendo claves por defecto.');
            return {
                price_remove_ads: 'price_remove_ads',
                price_premium_phrases: 'price_premium_phrases'
            };
        }

        const docRef = doc(db, "config", "stripe_products");
        const docSnap = await getDoc(docRef);

        if (docSnap.exists()) {
            return docSnap.data() as {[key: string]: string};
        } else {
            console.log("No se encontraron claves de productos en Firestore");
            // Devolver valores predeterminados si no hay datos en Firestore
            return {
                price_remove_ads: 'price_remove_ads',
                price_premium_phrases: 'price_premium_phrases'
            };
        }
    } catch (error) {
        console.error('Error obteniendo claves de productos:', error);
        // Devolver valores predeterminados en caso de error
        return {
            price_remove_ads: 'price_remove_ads',
            price_premium_phrases: 'price_premium_phrases'
        };
    }
};
